main{
    .userinformation__section{
        padding-top:30px;
        .userinformation__section__title{
            width: 80%;
            margin:0 auto;
            margin-bottom:55px;
            h2{
                font-size:32px;
                margin-bottom:20px;
            }            
            span{
                opacity: 0.6;
            }
        }
        .userinformation__section__box{
            display:grid;
            grid-template-columns: repeat(2,1fr);
            place-items: center;
            .userinformation__section__use{
                width: 78%; height: 180px;
                border:1px solid rgb(225,225,225);
                border-radius: 10px;
                margin-bottom:100px;
                p{
                    text-align: center;
                    padding-top:45px;
                }
                p:nth-child(2){
                    color:#a3a8e8;
                    font-size:14px;
                }
            }
            .userinformation__section__communication{
                opacity: 0.6;
            }
        }
    }
}