.loginheader{
    height: 30px;
    display:flex;
    justify-content: space-between;
    a{
        text-decoration: none;
    }
    button{
        width: 10%;
        background-color: white;
        border:none;
        svg{
            font-size:20px;
        }
    }
    .loginheader__home{
        display:flex;
        align-items: center;
        justify-content: center;
        width: 20%;
        font-size:24px;
        svg{
            color:black;
        }
    }
    .loginheader__login{
        display:flex;
        align-items: center;
        justify-content: center;
        width: 80%;
    } 
}
